import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {
  SearchForm,
  NavBar,
  About,
  Home,
  FinalProject,
  Classification,
} from './Components';
import PitchFork from './Components/Pitchfork';
import { BrowserRouter as Router, Route } from 'react-router-dom';

class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
          <NavBar />
          <div id="content">
            <Route exact path="/" component={Home} />
            <Route exact path="/predict" component={SearchForm} />
            <Route exact path="/about" component={About} />
            <Route exact path="/classification" component={Classification} />
            <Route exact path="/pitchfork" component={PitchFork} />
            <Route exact path="/finalProject" component={FinalProject} />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
